import React from 'react';
import { SingleItem } from '../../global-style/settingsSection';
import OptionButton from '../../my-button/option-button';
import { withRouter } from 'react-router-dom';
import DeleteButton from '../../my-button/delete-button/deleteBtn';

const SingleVatItem = ({ item, match, history }) => {
  const BooleanToHebrew = item.vat ? '' : '';
  return (
    <SingleItem>
      <span className='itemName'>{item.title}</span>
      <span className='itemName'>{item.duplicate}</span>
      <span className='itemName'>{BooleanToHebrew}</span>
      <div className='buttons'>
        <OptionButton
          onClick={() =>
            history.push(`${match.path}/updateVatItem/${item._id}`)
          }
          edit
        >
          &#9998;
        </OptionButton>
        <DeleteButton item={item} />
      </div>
    </SingleItem>
  );
};

export default withRouter(SingleVatItem);
